// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import resumeData from './assets/data/resumeData.js';
import config from './assets/config/index.js';
import axios from 'axios';

Vue.prototype.$http = axios;
Vue.prototype.$config = config;
Vue.use(ElementUI)

//向VUE中注入数据对象，也可以考虑使用vuex来做处理
//在online模式中 可以判断config中的JS  调整model数值  来完成resumeData的获取途径
if(config.model == 1){
  //线上模式  推荐使用 axios
  //参考地址 http://blog.csdn.net/binginsist/article/details/65630547

/*  Vue.$http.get('', {
    params: {

    }
  })
    .then(function (response) {
      console.log(response);
    })
    .catch(function (response) {
      console.log(response);
    });*/
}else if(config.model == 2){
  Vue.prototype.$resumeData=resumeData;
}else{
  Vue.prototype.$resumeData=resumeData;
}




Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  beforeCreate: function(){
    console.log("VUE 开始创建时根据设备跳转不同路由")
    let equipment = false;// true 移动端  false PC
    equipment=/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent);
    if(equipment){
      //M站 开发中 暂时都跳转到PC
      this.$router.push({ name: 'PCMain'});
     // this.$router.push({ name: 'MMain'});
    }else{
      this.$router.push({ name: 'PCMain'});
    }
  }
})



